<template><section><h1>描述</h1>
<p>此组件是提供了一个组件布局的方案，关于视频插件内部实现则使用prop的方式作传递，组件本身仅提供布局及焦点控制</p>
<h1>效果展示</h1>
<template>
<div>
  <bs-video :curPage="curPage" :count="3" style="height: 400px" :pluginCOM="plugin" :COMprops="[]" :styles="styles" :activedVM.sync="avm" ref="video"></bs-video>
  <v-btn @click="getCOM">getCOM</v-btn>
</div>
</template>


