<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>count</td>
<td>总渲染的插件数量(一次渲染不再变化)</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>showNum</td>
<td>每页显示的插件数量(styles属性生效时此属性无效)</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>curPage</td>
<td>当前页码(styles属性生效时此属性无效)</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>styles</td>
<td>每个插件的样式对象</td>
<td>Array[Object]</td>
<td>null</td>
</tr>
<tr>
<td>pluginCOM</td>
<td>插件的vue组件对象</td>
<td>Vue Object</td>
<td>必须</td>
</tr>
<tr>
<td>COMprops</td>
<td>实例化每个插件传递的props</td>
<td>Array[Object]</td>
<td>[]</td>
</tr>
</tbody>
</table>
<h1>events</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>描述</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>update:activedVM</td>
<td>切换焦点时返回当前焦点的vm对象</td>
<td>当前焦点的vm对象</td>
</tr>
</tbody>
</table>
<h1>vm对象获得的属性</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>值</th>
</tr>
</thead>
<tbody>
<tr>
<td>index</td>
<td>索引值</td>
<td>Number</td>
<td>index</td>
</tr>
<tr>
<td>$parent</td>
<td>父组件引用</td>
<td>VM Object</td>
<td>bs-video的vm对象</td>
</tr>
</tbody>
</table>
<p>以及由COMprops来的对象属性值</p>
<h1>methods</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>方法名称</th>
<th>描述</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>getCOM</td>
<td>获得某个下标的vm对象</td>
<td>index</td>
<td>index的vm对象</td>
</tr>
</tbody>
</table>
<h1>样式定义</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>类名</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>bs-video</td>
<td>video组件容器</td>
</tr>
<tr>
<td>bs-video-single</td>
<td>video单个插件容器</td>
</tr>
<tr>
<td>bs-actived</td>
<td>当前激活窗口的插件容器</td>
</tr>
</tbody>
</table>
<h1>插件事件</h1>
<p>默认的网页元素的click事件会更改activedIndex，如果点击到插件上就被拦截了</p>
<p>在插件的点击回调里添加this.$el.click()即可</p>
</section></template>
<style lang="stylus">
.bs-video-single
  background #eee
  border 1px solid #eee
.bs-actived
  border 1px solid red
</style><style lang="stylus">
