<script>
import plugin from './plugin.vue'
export default {
  data () {
    return {
      plugin,
      curPage: 0,
      showNum: 4,
      styles: [
        {
          width: '33%',
          height: '100%'
        }, {
          width: '33%',
          height: '100%'
        }, {
          width: '33%',
          height: '100%'
        }
      ],
      avm: null,
    }
  },
  computed: {
    videoPlugin() {
      return this.$refs.video
    }
  },
  watch: {
    avm(v) {
      console.log(v)
    }
  },
  methods: {
    getCOM() {
      this.videoPlugin.getCOM(2).aaa()
    }
  }
}
</script>


